import { Box, Button, Grid, useTheme } from '@mui/material'
import React, { useState } from 'react'
import Header from '../../shared/header'
import { Main } from '../../shared/main'
import Navigator from '../../shared/navigator'
import { ReactComponent as IconAddCircle } from '../../assets/icon/IconAddCircle.svg'
import Search from '../../shared/component/Search'
import WhitelistCard from './WhitelistCard'
import WhitelistInfoDialog from './WhitelistInfoDialog'
import { WhitelistInfo } from './types'
import { WHITELISTS } from './data'

const Whitelist: React.FC<{}> = () => {
  const theme = useTheme()
  const [showDialog, toggleDialog] = useState(false)
  const [whitelist, setWhitelist] = useState<null | WhitelistInfo>(null)

  return (
    <>
      <Header />
      <Navigator page='whitelist' />
      <Main>
        <Box sx={{ display: 'flex', justifyContent: 'end' }}>
          <Button
            color='secondary'
            startIcon={<IconAddCircle fill={theme.palette.secondary.main} />}
            sx={{
              background: '#FFFFFF',
              boxShadow: '0px 3px 6px #00000029',
              padding: '10px 17px',
              fontSize: '15px',
              fontWeight: 'normal',
              lineHeight: '20px',
            }}
            onClick={() => toggleDialog(true)}
          >
            Create whitelist
          </Button>

          <Box ml='15px' />
          <Search />
        </Box>

        <Box mt='47px' />
        <Grid container spacing={5}>
          {WHITELISTS.map((whitelist, idx) => (
            <Grid item key={idx}>
              <WhitelistCard
                whitelist={whitelist}
                onEdit={(data: WhitelistInfo) => {
                  toggleDialog(true)
                  setWhitelist(data)
                }}
              />
            </Grid>
          ))}
        </Grid>
      </Main>
      {showDialog && (
        <WhitelistInfoDialog
          whitelist={whitelist}
          onClose={() => {
            toggleDialog(false)
            setWhitelist(null)
          }}
        />
      )}
    </>
  )
}

export default Whitelist
