import {
  Box as _Box,
  Button,
  Dialog,
  DialogTitle,
  FormControl,
  IconButton,
  InputLabel,
  MenuItem,
  Paper,
  Select as _Select,
  SelectChangeEvent,
  styled,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField as _TextField,
  Typography,
  useTheme,
} from '@mui/material'
import dayjs from 'dayjs'
import React, { useCallback, useEffect, useState } from 'react'
import { ReactComponent as IconClose } from '../../assets/icon/IconClose.svg'
import { ReactComponent as IconConfirm } from '../../assets/icon/IconConfirm.svg'
import { Group, WhitelistInfo } from './types'

const DEFAULT_TZ = 'Asia/Taipei'
const NOW = dayjs().tz(DEFAULT_TZ)

const GROUPS = [
  { id: '1', name: 'Group 1', description: '30 characters' },
  { id: '2', name: 'Group 2', description: '123456789012345678901234567890' },
  { id: '3', name: 'Group 3', description: '123456789012345678901234567890' },
  { id: '4', name: 'Group 4', description: '123456789012345678901234567890' },
]

const Box = styled(_Box)({
  display: 'flex',
  alignItems: 'center',
})

const Label = styled(Typography)({
  width: '150px',
  textAlign: 'end',
  marginRight: '12px',
  fontWeight: 'bold',
})

const TextField = styled(_TextField)(({ theme }) => ({
  flexGrow: '1',
  '& .MuiOutlinedInput-root': {
    color: theme.palette.primary.main,
  },
  '& fieldset': {
    borderColor: theme.palette.secondary.main,
  },
}))

const Select = styled(_Select)(({ theme }) => ({
  flexGrow: '1',
  color: theme.palette.primary.main,
  '& fieldset': {
    borderColor: theme.palette.secondary.main,
  },
}))

function prepareGroupIds(groups?: Group[]) {
  if (!groups) return []

  const groupIds = groups.map((group) => group.id)
  return groupIds
}

function getGroupMap(groups: Group[]) {
  const newMap = new Map()
  groups.forEach((group) => newMap.set(group.id, { name: group.name, desc: group.description }))
  return newMap
}

const WhitelistInfoDialog: React.FC<{ whitelist: WhitelistInfo | null; onClose: () => void }> = ({
  whitelist,
  onClose,
}) => {
  const theme = useTheme()
  const [name, setName] = useState(whitelist?.name ?? '')
  const [desc, setDesc] = useState(whitelist?.description ?? '')
  const [ip, setIp] = useState(whitelist?.ip ?? '000.000.000.000')
  const [start, setStart] = useState(whitelist?.startTime ?? `${NOW.hour()}:00`)
  const [end, setEnd] = useState(whitelist?.endTime ?? `${NOW.hour()}:00`)
  const [timezone, setTimezone] = useState(whitelist?.timezone ?? DEFAULT_TZ)
  const [groupIds, setGroupIds] = useState<string[]>(prepareGroupIds(whitelist?.groups))
  const groupMap = getGroupMap(GROUPS)

  const isCreation = whitelist === null

  const onNameChange = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    const newName = event.target.value

    if (newName.length > 30) return
    setName(newName)
  }, [])

  const onDescChange = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    const newDesc = event.target.value

    if (newDesc.length > 100) return
    setDesc(newDesc)
  }, [])

  const onIpChange = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    const newIp = event.target.value
    setIp(newIp)
  }, [])

  const onStartTimeChange = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    setStart(event.target.value)
  }, [])

  const onEndTimeChange = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    setEnd(event.target.value)
  }, [])

  const onTimezoneChange = useCallback((event: SelectChangeEvent<unknown>) => {
    setTimezone(event.target.value as string)
  }, [])

  const onGroupsChange = useCallback((event: SelectChangeEvent<unknown>) => {
    setGroupIds(event.target.value as string[])
  }, [])

  return (
    <Dialog open={true}>
      <Paper sx={{ width: '543px', padding: '44px 26px 25px 38px' }}>
        <IconButton sx={{ position: 'absolute', top: '18px', right: '26px' }} onClick={onClose}>
          <IconClose />
        </IconButton>

        <DialogTitle
          color='primary'
          sx={{ fontWeight: 'bold', fontSize: '20px', lineHeight: '24px', paddingLeft: '0px' }}
        >
          {isCreation ? 'Create whitelist' : 'Edit whitelist'}
        </DialogTitle>

        <Box sx={{ borderTop: '1px solid #2E89FF80' }} />
        <Box mt='30px'>
          <Label color='primary'>Whitelist name: </Label>
          {isCreation ? (
            <TextField size='small' value={name} onChange={onNameChange} />
          ) : (
            <Typography color='primary'>{whitelist?.name}</Typography>
          )}
        </Box>

        <Box sx={{ display: 'flex', alignItems: 'start', marginTop: '17px' }}>
          <Label color='primary'>Description:</Label>
          <TextField
            size='small'
            placeholder='100 characters'
            multiline
            rows={3}
            value={desc}
            onChange={onDescChange}
          />
        </Box>

        <Box mt='20px'>
          <Label color='primary'>IP: </Label>
          <TextField size='small' value={ip} onChange={onIpChange} />
        </Box>

        <Box mt='19px'>
          <Label color='primary'>Time: </Label>
          <Box sx={{ flex: 1, justifyContent: 'space-between' }}>
            <FormControl sx={{ width: '64px' }}>
              <TextField size='small' value={start} onChange={onStartTimeChange} />
            </FormControl>
            <Label sx={{ width: 'max-content', margin: '0px' }}>To</Label>

            <FormControl sx={{ width: '64px' }}>
              <TextField size='small' value={end} onChange={onEndTimeChange} />
            </FormControl>
            <FormControl variant='outlined' sx={{ width: '140px' }} size='small'>
              <InputLabel>Time zone</InputLabel>
              <Select value={timezone} onChange={onTimezoneChange} size='small' label='Time zone'>
                <MenuItem value='Asia/Taipei' sx={{ color: theme.palette.primary.main }}>
                  Asia/Taipei
                </MenuItem>
                <MenuItem value='Asia/Hong_Kong' sx={{ color: theme.palette.primary.main }}>
                  Asia/Hong_Kong
                </MenuItem>
              </Select>
            </FormControl>
          </Box>
        </Box>

        <Box mt='24px'>
          <Label color='primary'>Group: </Label>
          <FormControl variant='outlined' sx={{ flexGrow: 1 }} size='small'>
            <InputLabel>Select group</InputLabel>
            <Select label='Select group' size='small' multiple value={groupIds} onChange={onGroupsChange}>
              {GROUPS.map((group) => (
                <MenuItem key={group.id} value={group.id} sx={{ color: theme.palette.primary.main }}>
                  {group.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Box>

        <Box mt='24px'>
          <TableContainer sx={{ border: `1px solid ${theme.palette.secondary.main}`, height: '120px' }}>
            <Table stickyHeader>
              <TableHead>
                <TableRow>
                  <TableCell
                    sx={{
                      backgroundColor: theme.palette.secondary.main,
                      color: '#FFFFFF',
                      pl: '23px',
                      py: '6px',
                      width: '150px',
                    }}
                  >
                    Group name
                  </TableCell>
                  <TableCell sx={{ backgroundColor: theme.palette.secondary.main, color: '#FFFFFF', py: '6px' }}>
                    Description
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {groupIds.map((id) => (
                  <TableRow key={id}>
                    <TableCell sx={{ pl: '23px', border: 'none', py: '3px' }}>{groupMap.get(id).name}</TableCell>
                    <TableCell sx={{ border: 'none', py: '3px' }}>{groupMap.get(id).desc}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>

        <Box sx={{ mt: '29px', justifyContent: 'end' }}>
          <Button variant='contained' color='secondary' startIcon={<IconConfirm fill='#FFFFFF' />} onClick={onClose}>
            Confirm
          </Button>
        </Box>
      </Paper>
    </Dialog>
  )
}

export default WhitelistInfoDialog
