import {
  Box,
  Button,
  Card,
  CardActionArea,
  CardContent,
  Grid,
  IconButton,
  styled,
  Typography as _Typography,
  useTheme,
} from '@mui/material'
import React, { useState } from 'react'
import { ReactComponent as IconUp } from '../../assets/icon/IconUp.svg'
import { ReactComponent as IconDown } from '../../assets/icon/IconDown.svg'
import { ReactComponent as IconEdit } from '../../assets/icon/IconEdit.svg'
import { WhitelistInfo } from './types'
import { generatePath, useNavigate } from 'react-router-dom'
import { ROUTES } from '../../constants'

const Typography = styled(_Typography)({
  fontSize: '15px',
  lineHeight: '18px',
  display: 'inline-block',
})

const WhitelistCard: React.FC<{
  whitelist: WhitelistInfo
  onEdit: (whitelist: WhitelistInfo) => void
}> = ({ whitelist, onEdit }) => {
  const theme = useTheme()
  const [isExpand, toggleExpand] = useState(true)
  const navigate = useNavigate()

  return (
    <Card
      sx={{
        width: '650px',
        boxShadow: '0px 3px 6px #00000029',
        position: 'relative',
      }}
    >
      <CardActionArea
        disabled={whitelist.readonly}
        onClick={() => {
          const path = generatePath(ROUTES.whitelist + '/:whitelistId', { whitelistId: whitelist.id })
          navigate(path)
        }}
      >
        <CardContent sx={{ padding: '23px 21px 21px 34px' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
            <Typography color='primary' sx={{ fontWeight: 'bold', fontSize: '20px', lineHeight: '24px' }}>
              {whitelist.name}
            </Typography>
          </Box>

          <Box mt='18px' />
          <Typography
            sx={{
              background: theme.palette.secondary.main,
              color: '#FFFFFF',
              padding: '5px 14px',
            }}
          >
            {`Website domain: ${whitelist.domainCount}`}
          </Typography>

          <Box mt='18px' />
          <Box sx={{ minHeight: '54px' }}>
            <Typography color='secondary'>{`Description: ${whitelist.description}`}</Typography>
          </Box>

          <Box mt='10px' mb='12px' sx={{ borderTop: '1px solid #96C4FF' }} />

          <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
            <Typography color='primary'>Apply to</Typography>
          </Box>

          {isExpand && (
            <>
              <Box mt='12px' />
              <Grid container>
                <Typography
                  color='secondary'
                  sx={{ display: 'flex', alignItems: 'center', justifyContent: 'end', width: '48px' }}
                >
                  Group:
                </Typography>

                <Box ml='6px' />
                {whitelist.readonly ? (
                  <Typography
                    color='secondary'
                    sx={{
                      padding: '6px 9px',
                      border: `1px solid ${theme.palette.secondary.main}`,
                      borderRadius: '20px',
                      boxShadow: '0px 3px 6px #00000029',
                    }}
                  >
                    All user groups
                  </Typography>
                ) : (
                  whitelist.groups.map((group) => (
                    <Grid item key={group.id} xs='auto' sx={{ padding: '3px' }}>
                      <Typography
                        color='secondary'
                        sx={{
                          padding: '6px 9px',
                          border: `1px solid ${theme.palette.secondary.main}`,
                          borderRadius: '20px',
                          boxShadow: '0px 3px 6px #00000029',
                        }}
                      >
                        {group.name}
                      </Typography>
                    </Grid>
                  ))
                )}
              </Grid>

              <Box mt='10px' />
              <Typography color='secondary' sx={{ textAlign: 'end', width: '48px' }}>
                IP:
              </Typography>
              <Typography
                color='secondary'
                sx={{
                  ml: '6px',
                  padding: '6px 9px',
                  border: `1px solid ${theme.palette.secondary.main}`,
                  borderRadius: '20px',
                  boxShadow: '0px 3px 6px #00000029',
                }}
              >
                {whitelist.ip}
              </Typography>
            </>
          )}
        </CardContent>
      </CardActionArea>

      {!whitelist.readonly && (
        <Button
          color='secondary'
          sx={{
            fontSize: '15px',
            lineHeight: '18px',
            fontWeight: 'normal',
            position: 'absolute',
            top: '21px',
            right: '16px',
          }}
          startIcon={<IconEdit />}
          onClick={(e) => {
            e.stopPropagation()
            onEdit(whitelist)
          }}
        >
          Edit
        </Button>
      )}

      <IconButton
        onClick={(e) => {
          e.stopPropagation()
          toggleExpand((isExpand) => !isExpand)
        }}
        sx={{ position: 'absolute', top: '185px', right: '23px' }}
      >
        {isExpand ? <IconUp /> : <IconDown />}
      </IconButton>
    </Card>
  )
}

export default WhitelistCard
